import React from "react"

import { Container, Row, Col } from "reusecore/Layout"
import SectionTitle from "reusecore/SectionTitle"
import Button from "reusecore/Button"
import VintageBox from "reusecore/VintageBox"
import Particle from "reusecore/Particle"

import { FaPlay, FaDownload } from "react-icons/fa"

import BannerSectionWrapper from "./banner.style"

const BannerDefault = () => {
  return (
    <BannerSectionWrapper>
      <Particle />
      <Container fullWidthSM>
        <Row>
          <Col sm={7} md={6} lg={7}>
            <SectionTitle
              className="section-title"
              leftAlign={true}
              UniWidth="100%"
            >
              <h1>
                Software Consulting and Development for Your Digital
                <br />
                <span>Success</span>
              </h1>
            </SectionTitle>
            <p>
              We believe in technology and it should reach everyone regardless
              of how fatter their valet is. So we saw a need for modern
              technology for everyone at affordable price, and we knew only
              dedicated people with correct mindset can do that.
            </p>
            {/* <VintageBox right={true} vintageOne={true}>
              <Button className="banner-btn one">
                <FaDownload className="icon-left" />
                Download V1.4
              </Button>
              <Button className="banner-btn two">
                <FaPlay className="icon-left" /> Watch Now
              </Button>
            </VintageBox> */}
          </Col>
        </Row>
      </Container>
    </BannerSectionWrapper>
  )
}

export default BannerDefault
